* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  user-select: none;
  background-color: black;
}

.aa {
  opacity: 0.2;

}
.uui {
  position: absolute !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-items: center;
}
.canvas1 {
  position: absolute !important;
  padding: 0px !important;
  margin: 0px !important;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: transparent;
}

.dark-effect::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Dark effect */
  z-index: 2;
  pointer-events: none;
}


